import * as console from "console";
import { DayOfWeek, Period } from "src/infrastructure/utils/enums/date.enum";

import IMachineSellingPoint from "@application/interfaces/machine/machine-selling-point.interface";
import { BadRequestError } from "@presentation/errors/http-errors/bad-request-error";

export class MachineSellingPointValidator {
  sellingPointId: string;
  machineId: string;
  cnpj?: string;
  conciliation?: {
    companyData: {
      name: string;
      cnpj: string;
      email: string;
      responsibleEstablishment: string;
      usedLayout: string;
      homologated: boolean;
    };
    itContact: {
      name: string;
      email: string;
      phone: string;
    };
    eftProvider: {
      providerTef: string;
      connectionType: string;
    };
    integratingCompany: {
      name: string;
      email: string;
      phone: string;
      itResponsible: string;
      itEmail: string;
      itPhone: string;
    };
    activation: {
      daysWeek: DayOfWeek;
      period: Period;
      endDate: Date;
      startDate: Date;
      observation: string;
    };
  };

  constructor(json: any) {
    try {
      const { sellingPointId, machineId, conciliation, cnpj } = JSON.parse(
        JSON.stringify(json)
      );

      this.sellingPointId = sellingPointId;
      this.sellingPointId = machineId;
      this.conciliation = conciliation;
      this.cnpj = cnpj;
      this.isReady();
    } catch (e) {
      console.error(e);
      throw new BadRequestError(
        94,
        "Body não identificado",
        e,
        "sellingPointId ou littleMachineId não localizados"
      );
    }
  }

  isReady() {
    if (!this.machineId || !this.sellingPointId) {
      throw new BadRequestError(
        94,
        "Body não identificado",
        "littleMachineId ou littleMachineId não localizados"
      );
    }
  }

  isValid() {
    return this as IMachineSellingPoint;
  }
}
