import { APIGatewayProxyEvent } from "aws-lambda";
import { middyfy } from "resources/lambda";
import { container } from "tsyringe";

import { SuccessResponse } from "@adapter/responses";
import { LambdaHandler } from "@infrastructure/middleware/lambda";
import MachineController from "@presentation/controllers/machine/MachineController";

const lambdaHandler = container.resolve(LambdaHandler);

const putPointOfSalesHandler = async (event: APIGatewayProxyEvent) =>
  lambdaHandler.perform(event, "POST", async () => {
    const controller = new MachineController();
    return new SuccessResponse(
      await controller.sellingPointService(event.body)
    );
  });

export const main = middyfy(putPointOfSalesHandler);
