import { MachineSellingPointValidator } from "src/presentation/validators/machine/machine-selling-point.validator";

import { MachineSellingPointService } from "@application/services/machine/machine-selling-point-app.service";
import { container } from "@infrastructure/config";

export default class MachineController {
  private machineSellingPointService: MachineSellingPointService;

  constructor() {
    this.machineSellingPointService = container.resolve(
      MachineSellingPointService
    );
  }

  async sellingPointService(input: any): Promise<void> {
    const data = new MachineSellingPointValidator(input).isValid();
    await this.machineSellingPointService.execute(data);
  }
}
